.title {
    font-size: 20px;
    font-family: 'Montserrat', sans-serif;
    
}

.header3 {
    font-size: 25px;
    text-align: center;
    margin: auto;
    max-width: 600px;
    padding: 0.5rem 1rem;
    font-family: 'Montserrat', sans-serif;

}

.a4i {
    font-size: 20px;
    text-align: left;
    margin: auto;
    max-width: 600px;
    min-width: 750px;
    padding: 1rem 0rem;
    font-family: 'Montserrat', sans-serif;
    float: right;
    font-weight: bolder;

}

.descriptionexperience
{
    font-size: 100%;
    text-align: left;
    margin: auto;
    max-width: 300px;
    min-width: 750px;
    padding: 0.75rem 1rem;
    font-family: 'Montserrat', sans-serif;
    float: right;
    font-weight: bolder;
}

.column {
    float: left;
    width: 45%;
    height: 500px;
}

.row:after {
    content: "";
    display: table;
    clear: both;
  }

.jobtitle
{
    font-size: 20px;
    text-align: left;
    margin: auto;
    max-width: 600px;
    min-width: 750px;
    padding: 0rem 1rem;
    font-family: 'Montserrat', sans-serif;
    float: right;
    font-weight: bolder;
}

.datetime
{
    font-size: 20px;
    text-align: left;
    margin: auto;
    max-width: 600px;
    min-width: 750px;
    padding: 0.25rem 1rem;
    font-family: 'Montserrat', sans-serif;
    float: right;
    font-weight: bolder;
}

.education {
    height: 220px;
    float: left;
    width: 100%;
    padding: 20px;
    margin-left: 50px;
    margin-bottom: 50px;
    
    }

.cambodia {
    height: 350px;
    float: left;
    width: 100%;
    display: table;
    vertical-align: middle;
    padding: 50px;
    margin-bottom: 50px;
     }

     @media screen and (max-width: 800px) {
        .column {
          width: 100%;
          padding: 0;
        }
        .main {
            width: 100%;
            padding: 0;
        }
        .descriptionexperience {
            width: 200%;
            font-size: 100%;
            padding: 0;
        }
    }